import Cart from "./Cart";
import ProductIndex from "./ProductIndex";
import ProductList from "./ProductList";
import ProductDetail from "./ProductDetail";
import ProductCategory from "./ProductCategory";


module.exports = [
    {path: '/cart', component: Cart, meta: {title: '', auth: false}},
    {path: '/product/index', component: ProductIndex, meta: {title: '', auth: false}},
    {path: '/product/list', component: ProductList, meta: {title: '', auth: false}},
    {path: '/product/category', component: ProductCategory, meta: {title: '', auth: false}},
    {path: '/product/detail/:itemid', component: ProductDetail, meta: {title: '', auth: false}},
]
