<template>
    <div class="container">
        <van-cell-group>
            <van-cell size="large">
                <strong slot="title"></strong>
            </van-cell>
            <van-cell title="" :value="shipping.name"></van-cell>
            <van-cell title="" :value="shipping.tel"></van-cell>
            <van-cell title="" :value="shipping.full_address"></van-cell>
        </van-cell-group>
        <div class="h10"></div>
        <van-cell-group>
            <van-cell size="large">
                <strong slot="title"></strong>
            </van-cell>
            <van-field
                    v-model="shipping.express_name"
                    label=""
                    placeholder=""
                    :rules="[{ required: true, message:'' }]"
                    readonly
                    @click="showPopup=true"
            />
            <van-field
                    v-model="shipping.express_no"
                    type="text"
                    label=""
                    placeholder=""
                    :rules="[{ required: true, message:''}]"
            >
                <span class="iconfont icon-scan font-24" slot="right-icon" @click="onScan"></span>
            </van-field>
        </van-cell-group>
        <div style="padding: 15px;">
            <van-button round type="info" class="w-100" @click="onSubmit"></van-button>
        </div>
        <van-popup position="bottom" v-model="showPopup">
            <van-picker
                    :columns="expresses"
                    show-toolbar
                    title=""
                    @confirm="onChooseExpress"
                    @cancel="showPopup = false"
            />
        </van-popup>
    </div>
</template>

<script>
    export default {
        name: "RefundSend",
        data() {
            return {
                refund: {},
                items: {},
                shipping: {},
                expresses: [],
                showPopup: false
            }
        },
        mounted() {
            const {refund_id} = this.$route.query;
            this.$get('/refund/get', {refund_id}).then(response => {
                const {refund} = response.result;
                const {items, images, order, shipping} = refund;
                this.refund = refund;
                this.items = items;
                this.shipping = shipping || {};
            });
            this.fetchExpress();
        },
        methods: {
            fetchExpress() {
                this.$get('/express/getall').then(response => {
                    this.expresses = response.result.items.map((d) => {
                        return {
                            text: d.name,
                            value: d
                        }
                    });
                });
            },
            onSubmit() {
                const {refund_id} = this.refund;
                const {express_name, express_no} = this.shipping;
                if (!express_name) {
                    this.$showToast('');
                    return false;
                }

                if (!express_no) {
                    this.$showToast('');
                    return false;
                }

                this.$post('/refund/send', {refund_id, shipping: this.shipping}).then(response => {
                    this.$showToast('', () => {
                        window.history.go(-1);
                    });
                });
            },
            onScan() {
                let _this = this;
                let shipping = this.shipping;
                wx.scanQRCode({
                    needResult: 1, // 01
                    scanType: ["qrCode", "barCode"], // 
                    success: function (res) {
                        //var result = res.resultStr; // needResult  1 
                        var arr = res.resultStr.split(',');
                        if (arr[1]) {
                            shipping.express_no = arr[1];
                        } else {
                            shipping.express_no = res.resultStr;
                        }
                        _this.shipping = shipping;
                    }
                });
            },
            onChooseExpress(v) {
                this.shipping.express_name = v.value.name;
                this.shipping.express_code = v.value.code;
                this.showPopup = false;
            },
        }
    }
</script>

