<template>
    <div class="sign-container">
        <div class="sign-content">
            <!--            <div class="sign-logo">-->
            <!--                <img src="/images/app/appicon.png" alt=""/>-->
            <!--            </div>-->
            <div class="sign-header">
                <h1></h1>
            </div>
            <div class="sign-form">
                <van-field
                        type="tel"
                        class="van-field-large"
                        placeholder=""
                        v-model="account"
                />
                <van-field
                        type="password"
                        class="van-field-large"
                        placeholder=""
                        v-model="password"
                />
                <div class="button-container">
                    <van-button type="danger" round block :disabled="disabled" @click="submit"></van-button>
                </div>
            </div>
            <div class="policy-tip">
                <div class="flex-fill">
                    <input type="checkbox" style="vertical-align: -2px;" v-model="agreement">
                    <span><a href="/h5/page/36.html" target="_blank"></a></span>
                </div>
                <div>
                    <a @click="showVerCodeLogin"></a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {OauthClientId, OauthClientSecret} from "../config";
    import {LOGIN} from "../vuex/types";

    export default {
        name: "Login",
        data() {
            return {
                account: '',
                password: '',
                agreement: true
            }
        },
        computed: {
            disabled() {
                return !(this.account.length > 2 && this.password.length > 5);
            }
        },
        methods: {
            submit() {
                let {account, password} = this;
                if (!account) {
                    this.$toast.fail('');
                    return false;
                }

                if (!password) {
                    this.$toast.fail('');
                    return false;
                }

                if (!this.agreement) {
                    this.$toast.fail('');
                    return false;
                }

                this.$axios.post('/oauth/token', {
                    'grant_type': 'password',
                    'client_id': OauthClientId,
                    'client_secret': OauthClientSecret,
                    'username': account,
                    'password': password,
                    'scope': '*',
                }).then(response => {
                    const {access_token} = response.data;
                    if (access_token) {
                        localStorage.setItem('accessToken', access_token);
                        this.$axios.defaults.headers.common['Authorization'] = 'Bearer ' + access_token;
                        this.$get('/user/info').then(response => {
                            this.$store.commit(LOGIN, response.result.userInfo);
                        });
                        this.$router.go(-1);
                    }
                }).catch(reason => {
                    this.$toast.fail('');
                });
            },
            showVerCodeLogin() {
                this.$router.replace('/ver_code_login');
            }
        }
    }
</script>

