<template>
    <van-form @submit="onSubmit">
        <van-field
                v-model="title"
                name="title"
                placeholder=""
                :rules="[{ required: true}]"
                class="van-field-large"
        />
        <van-field
                v-model="message"
                type="textarea"
                name="message"
                placeholder=""
                :rules="[{ required: true }]"
                class="van-field-large"
                :rows="5"
        />
        <div style="margin: 16px;">
            <van-button block type="info" native-type="submit">
                
            </van-button>
        </div>
    </van-form>
</template>

<script>
    export default {
        name: "FeedBack",
        data: function () {
            return {
                title: '',
                message: ''
            }
        },
        methods: {
            onSubmit: function (e) {
                const {title,message} = e;
                if (title && message){
                    this.$axios.post('/h5/feedback',{title,message}).then(response=>{
                        this.$toast.success('');
                        this.title = '';
                        this.message = '';
                    });
                }
            }
        }
    }
</script>

