<template>
    <div class="refund-detail">
        <h3>{{refund.refund_state_des}}</h3>
        <ul class="tips">
            <li></li>
            <li></li>
        </ul>
        <div class="op-buttons">
            <van-button size="small" @click="handleRevoke" v-if="refund.refund_state<5"></van-button>
            <van-button size="small" type="info" plain @click="handleEdit" v-if="refund.refund_state<3">
            </van-button>
            <van-button size="small" type="info" plain @click="handleSend" v-if="refund.refund_state===3">
            </van-button>
        </div>
        <div class="h30"></div>
        <h3></h3>
        <div class="refund-info">
            <dl>
                <dt>:</dt>
                <dd>{{order.order_no}}</dd>
            </dl>
            <dl>
                <dt>:</dt>
                <dd>{{refund.refund_type_des}}</dd>
            </dl>
            <dl>
                <dt>:</dt>
                <dd>{{refund.refund_no}}</dd>
            </dl>
            <dl>
                <dt>:</dt>
                <dd>{{refund.refund_reason}}</dd>
            </dl>
            <dl>
                <dt>:</dt>
                <dd>{{refund.refund_amount}}</dd>
            </dl>
            <dl>
                <dt>:</dt>
                <dd>{{refund.created_at}}</dd>
            </dl>
            <dl v-if="refund.refund_desc">
                <dt>:</dt>
                <dd>{{refund.refund_desc}}</dd>
            </dl>
        </div>
    </div>
</template>

<script>
    export default {
        name: "RefundDetail",
        data() {
            return {
                refund: {},
                order: {}
            }
        },
        mounted() {
            const {refund_id} = this.$route.query;
            this.$get('/refund/get', {refund_id}).then(response => {
                const {refund} = response.result;
                this.refund = refund;
                this.order = refund.order;
            });
        },
        methods: {
            handleRevoke() {
                this.$dialog.confirm({
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning',
                    message: '?'
                }).then(() => {
                    const {refund_id} = this.refund;
                    this.$post('/refund/revoke', {refund_id}).then(response => {
                        window.history.go(-1);
                    });
                }).catch(reason => {

                });
            },
            handleEdit() {
                const {refund_id} = this.refund;
                window.location.href = 'apply?refund_id=' + refund_id;
            },
            handleSend() {
                const {refund_id} = this.refund;
                window.location.href = 'send?refund_id=' + refund_id;
            }
        }
    }
</script>

